<template>
  <div v-if="profile" class="box">
    <article class="media">
      <div class="media-left">
        <figure class="image is-64x64">
          <img src="https://bulma.io/images/placeholders/128x128.png" alt="Image" />
        </figure>
      </div>
      <div class="media-content">
        <div class="content">
          <p>
            <strong>{{ profile.alias }}</strong>

            <br />Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean efficitur sit amet massa fringilla egestas. Nullam condimentum luctus turpis.
          </p>
        </div>
        <nav class="level is-mobile">
          <div class="level-left">
            <a class="level-item" aria-label="reply">
              <span class="icon is-small">
                <i class="fas fa-reply" aria-hidden="true"></i>
              </span>
            </a>
            <a class="level-item" aria-label="retweet">
              <span class="icon is-small">
                <i class="fas fa-retweet" aria-hidden="true"></i>
              </span>
            </a>
            <a class="level-item" aria-label="like">
              <span class="icon is-small">
                <i class="fas fa-heart" aria-hidden="true"></i>
              </span>
            </a>
          </div>
        </nav>
      </div>
    </article>
    <p
      class="subtitle has-text-dark has-background-primary-light"
    >{{comments.length ? 'Comments' : 'no comments yet'}}</p>
    <article
      v-for="(comment, index) in comments"
      :key="index"
      class="message"
      :class="comment.from == user.alias ? 'is-link fromCurrentUserMessageStyle' : 'is-primary toCurrentUserMessageStyle' "
    >
      <div class="message-header">
        <p>
          <i>from:</i>
          {{comment.from}}
        </p>
      </div>
      <div class="message-body">{{comment.content}}</div>
    </article>

    <form @submit.prevent="addComment">
      <div class="field">
        <label class="label has-background-primary-light">Add Comments</label>
        <div class="control has-icons-left has-icons-right">
          <input
            v-model="newComment"
            class="input is-success"
            type="text"
            placeholder="enter your comment..."
          />
          <span class="icon is-small is-right">
            <i class="fas fa-check"></i>
          </span>
        </div>
        <p class="help is-danger">{{feedback}}</p>
      </div>
    </form>
  </div>
</template>

<script>
import { db, auth } from "@/firebase/init";
export default {
  name: "UserProfile.vue",
  data() {
    return {
      profile: null,
      newComment: null,
      feedback: null,
      user: null,
      comments: []
    };
  },
  methods: {
    addComment() {
      if (this.newComment.trim()) {
        this.feedback = null;
        db.collection("geoComments")
          .add({
            to: this.$route.params.id,
            from: this.user.alias,
            content: this.newComment.trim(),
            time: Date.now()
          })
          .then(() => (this.newComment = null));
      } else {
        this.feedback = "You must enter a comment to do it";
      }
    }
  },
  created() {
    let ref = db.collection("geoUsers");

    // получим документ текущего пользователя(который пишет коментарий)
    ref
      .where("user_id", "==", auth.currentUser.uid)
      .get()
      .then(snapshot => {
        snapshot.forEach(doc => {
          this.user = doc.data();
          this.user.id = doc.id;
        });
      });

    // получим документ пользователя, кому отправляем
    ref
      .doc(this.$route.params.id)
      .get()
      .then(user => (this.profile = user.data()));

    // получение комментариев
    db.collection("geoComments")
      .orderBy("time")
      .limit(10)
      .where("to", "==", this.$route.params.id)
      .onSnapshot(snapshot => {
        snapshot.docChanges().forEach(change => {
          if (change.type === "added") {
            this.comments.unshift({
              from: change.doc.data().from,
              content: change.doc.data().content,
              time: change.doc.data().time
            });
          }
        });
      });
  }
};
</script>

<style lang="scss" scoped>
.fromCurrentUserMessageStyle {
  margin-left: 40%;
}
.toCurrentUserMessageStyle {
  margin-right: 40%;
}
</style>